<template>
  <v-app>
  <v-container>
    <h2 v-html="message" class="center-text"></h2>
    <div 
      v-for="page in pages" 
      :key="page.id"
      @click="goToPage(page.id)"
      class="pointer"
      >
      {{page.description}} - 
      {{formatDate(page.datetime)}}<br/><br/>
    </div>
  </v-container>
  </v-app>
</template>

<script>
import mixins from '@/mixins'

export default {
  data () {
    return {
      message: 'Pages',
      sortBy: 'description',
      searchFor: ''
    }
  },
  mixins: [mixins],
  computed: {
    pages: function() {
      let pages = this.$store.getters.pages
      pages = pages.sort( this.compareElements )
      return pages 
    }
  },
  methods: {
    goToPage (id) {
      this.$router.push("/page/" + id)
    },
  },
  created () {
    this.$store.dispatch('getPages')
  }
}
</script>

